  }

  .display-spaces, .game-pieces {
    width: 45px;
    height: 45px;
  }

  .dead-homies-white{
    left: 1%;
  float: left;
  padding-left: 0;
  top: 210px;
  }

  .dead-homies-black{
  left: 0;
padding-right: 0;
  float: right;
  top: 210px;
  }
  
  ul{
    width: 50px;
    margin: 0;
  }
}

